import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';

import ExamForm from '../components/ExamForm';
import ShowNotification from '../components/ShowNotification';
import DataTable from '../components/DataTable';

import getCookie from '../utils/getCookie';

@inject('classesStore')
@inject('subjectStore')
@inject('examGroupStore')
@inject('examStore')
export default class Exams extends Component {
  constructor(props){
    super(props);
    this.state = { classes: null, class_name: '', subject_name: '', date: '', examGroupDetails: null };
    this.dataTable = React.createRef();

    var self = this;
    window.examListActionEdit = this.edit;
    window.examListActionShowResults = this.showResults;
    let examGroup = this.props.match.params['examGroupId'];
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax : function (params, callback, settings) {
          self.props.examStore.list({exam_group_id: examGroup}, true).then(exams=>{
              callback({data: exams});
              return exams;
          })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: 'subject_name', name: 'subject_name', 'class': 'text-center'},
        { data: 'total', name: 'total', 'class': 'text-center'},
        { data: 'percentage', name: 'percentage', 'class': 'text-center'},
        { data: 'class_name', name: 'class', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
            <a onclick="examListActionShowResults('${data.id}')" class="btn btn-link btn-warning details"><i class="nc-icon nc-bullet-list-67 icon-bold"></i></a>
          </span>`}},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
            <a onclick="examListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
          </span>`}
        }]
    };
  }

  inputChanged(field,event) {
    let val = event.target.value;
    this.setState({[field]: val}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.table.column(4).search(this.state.class_name || '');
    this.dataTable.current.table.column(1).search(this.state.subject_name || '');
    this.dataTable.current.table.draw();
  }

  componentDidMount() {
    this.props.classesStore.list(true).then(classes=>{
      this.setState({classes});
    });

    this.props.subjectStore.list(true).then(subjects=>{
      this.setState({subjects});
    });

    this.props.examGroupStore.get(this.props.match.params.examGroupId).then(examGroup=>{
      this.setState({examGroupDetails: examGroup});
    })

  }

  edit = (exam) => {
    this.props.history.push(`${this.props.match.url}/${exam}`);
  }

  showResults = (exam) => {
    this.props.history.push(`${this.props.match.url}/${exam}/record`);
  }

  render() {
    let match = this.props.match;

    let {classes, subjects} = this.state;
    let title = `${this.state.examGroupDetails ? this.state.examGroupDetails.name : ''} Exams`;

    return <div className="container-fluid">
      <div className="row">
        <div className="col-md-12">
          <span className="pull-right">
            <Link to={`${match.url}/new`} className="btn btn-success details" >New Exam</Link>
          </span>
        </div>
      </div>
      <DataTable ref={this.dataTable} config={this.dataTableConfig} title={title} toolbar={
        <>
          <select onChange={this.inputChanged.bind(this,'class_name')} value={this.state.class_name} id='filterByClass' className="form-control col-6">
            <option value=''>-- Select Classes--</option>
            {classes && classes.map( (cls) => <option key={cls.id} value={cls.name}>{cls.name}</option> )}
          </select>
          <select onChange={this.inputChanged.bind(this,'subject_name')} value={this.state.subject_name} id='filterBySubject' className="form-control col-6">
            <option value=''>-- Select Subject--</option>
            {subjects && subjects.map( (subject) => <option key={subject.id} value={subject.name}>{subject.name}</option> )}
          </select>
        </>
      }>
        <th>Name</th>
        <th>Subject</th>
        <th>Total</th>
        <th>Percentage</th>
        <th>Class</th>
        <th>Results</th>
        <th>Action</th>
      </DataTable>
    </div>

  }
}

@inject('examStore')
export class NewExam extends Component {
  state = { error: {} };
  submit = (data) => {
    data.exam_group_id = this.props.match.params['examGroupId'];
    return this.props.examStore.create(data).then( exam => {
      this.setState({error: {}, data: exam})
      return exam;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    let examGroupId = this.props.match.params['examGroupId'];
    this.props.history.push(`/school/examination/exam-groups/${examGroupId}/exams`);
  }
  render() {
    return <ExamForm error={this.state.error} submit={this.submit} close={this.close} />
  }
}

@inject('examStore')
export class EditExam extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
    if(this.props.location.state && this.props.location.state.exam){
      this.setState({data: this.props.location.state.exam})
    }else if(this.props.match.params.examId){
      this.props.examStore.get(this.props.match.params.examId, true).then(exam => {
        this.setState({data: exam});
      },error=>this.setState({error}))
    }
  
  }

  submit = (data) => {
    return this.props.examStore.update(data).then( exam => {
      this.setState({error: {}, data: exam})
      return exam;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  delete = () => {
    return this.props.examStore.delete(this.props.match.params.examId);
  }

  close = () => {
    let examGroupId = this.props.match.params['examGroupId'];
    this.props.history.push(`/school/examination/exam-groups/${examGroupId}/exams`);
  }

  render() {
    return <ExamForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} close={this.close} />
  }
}