import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import lodash from 'lodash';
import $ from 'jquery';

import ShowNotification from '../components/ShowNotification';
import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';


@inject('classesStore')
@inject('examStore')
@inject('recordStore')
export default class Record extends Component {
  state = {examDetails: null, cls: null}
  dataTable = null;
  constructor(props) {
    super(props);
    var self = this;
    this.dataTable = React.createRef();
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax : function (params, callback, settings) {
          self.props.recordStore.list({exam_id: self.props.match.params.examId}, true).then(results=>{
              callback({data: results});
              return results;
          })
      },
      columns: [
        { data: 'firstname', name: 'firstname', 'class': 'text-center'},
        { data: 'lastname', name: 'lastname', 'class': 'text-center'},
        { data: 'stream_name', name: 'stream_name', 'class': 'text-center'},
        { data: 'marks', name: 'marks', 'class': 'text-center editable', render: (data,type,row) => row.marks ? row.marks : '-' }
      ]

    };

  }
  componentDidMount() {

    this.dataTable.current.table.MakeCellsEditable({
      onUpdate: this.updateValue,
      columns: [3]
    });

    this.props.examStore.get(this.props.match.params.examId).then( async(exam) =>{
      this.setState({examDetails: exam});
      let cls = await this.props.classesStore.get(exam.class_id);
      this.setState({cls});
    })
  }

  updateValue = (updatedCell, updatedRow, oldValue) => {
    let marks = updatedCell.data();
    let student_id = updatedRow.data()['student_id'];
    let record = updatedRow.data();
    let exam_id = this.props.match.params.examId;
    var self = this;
    if(record.pending || oldValue != marks){
      return this.props.recordStore.update({...record, marks}).then(recordResponse=>{
        updatedRow.data(recordResponse).draw();
      }, error=>{
        record['__meta_pending'] = true;
        record['__meta_error'] = error;
        updatedRow.data(record).draw();
        if(error.response && error.response.data && error.response.data.errors.marks){
          ShowNotification(error.response.data.errors.marks[0],"danger", "nc-simple-remove");
        }
      });
    }
  }

  filterStream = (event) => {
    let stream_name = event.target.value;
    this.setState({stream_name}, () => {
      this.dataTable.current.table.column(2).search(stream_name || '').draw();
    });
  }

  filterString = (event) => {
    let value = event.target.value;
    this.dataTable.current.table.search(value).draw();
  }

  render() {
    let match = this.props.match;
    let classes = this.state.classes;
    let streams = this.state.cls ? this.state.cls.streams : [];

    let title = `Record ${this.state.examDetails ? this.state.examDetails.name : ''}`;
    return <div className="container-fluid">
      <div className="row">
      <div className="col-md-12">
      </div>
      </div>
      <DataTable ref={this.dataTable} title={title} config={this.dataTableConfig} toolbar={
        <>
          <select onChange={this.filterStream} value={this.state.stream_name} id='filterByStream' className="form-control col-6">
            <option value=''>-- Select Stream--</option>
            {streams.map( (stream) => <option key={stream.id} value={stream.name}>{stream.name}</option> )}
          </select>
          <div className="form-group col-6">
            <input type="text" className="form-control" onChange={this.filterString} placeholder="Search" />
          </div>
        </>
      }>
        <th>Firstname</th>
        <th>Lastname</th>
        <th>Stream</th>
        <th>Marks</th>
      </DataTable>
    </div>

  }
}
