import React, { Component } from 'react';
import { inject } from 'mobx-react';
import { Link } from 'react-router-dom';

import DataTable from '../components/DataTable';
import getCookie from '../utils/getCookie';

@inject('subjectStore')
@inject('classesStore')
@inject('performanceStore')
export default class Performance extends Component {
  constructor(props) {
    super(props);
    var self = this;
    this.dataTable = React.createRef();
    this.state = {
      columns: [],
      dataTableConfig: {
        serverSide: false,
        processing: false,
        ajax : function (params, callback, settings) {
          self.props.performanceStore.list({class_id: self.state.class_id}, true).then(performances=>{
              callback({data: performances});
          });
        },
        columns: []
      }
    };
  }
  componentDidMount() {
    let columns = [
      { data: 'firstname', name: 'firstname', 'class': 'text-center editable'},
      { data: 'lastname', name: 'lastname', 'class': 'text-center editable'},
      { data: 'stream_name', name: 'stream_name', 'class': 'text-center editable'},
    ];
    this.props.subjectStore.list(true).then(subjects=>{
      for(let subject of subjects) {
        columns.push({data: ( row, type, set, meta ) => {
          if(!row[subject.shorthand])
            return '-';

          return row[`${subject.shorthand}_grade`];
        }, name: subject.shorthand, class: 'text-center editable'})
      }
      columns.push({data: ( row, type, set, meta ) => {
        if(!row['total'])
          return '-';

        return row['grade'];
      }, name: 'total', class: 'text-center editable'},
      {
        data: null, name:'Action', 'class': 'text-center', render: function(data,type,row) {
              return `<span>
                <a href="/report-card/${data.id}" target="_blank" class="btn btn-link btn-warning"><i class="fa fa-print"></i></a>
            </span>`
          }
      })

      let dataTableConfig = this.state.dataTableConfig;
      dataTableConfig.columns = columns;
      this.setState({dataTableConfig});
    });

    this.props.classesStore.list(true).then(classes=>{
      this.setState({classes});
    });
  }

  filterClass = () => {
    this.setState({class_id: event.target.value, stream_name: ''}, () => {
      this.dataTable.current.table.column(2).search('');
      this.dataTable.current.table.ajax.reload();
    });
  }

  filterStream = () => {
    this.setState({stream_name: event.target.value}, () => {
      this.dataTable.current.table.column(2).search(this.state.stream_name).draw();
    });
  }

  filterString = (event) => {
    let value = event.target.value;
    this.dataTable.current.table.search(value).draw();
  }

  render() {
    let match = this.props.match;
    let classes = this.state.classes;
    let streams = null;
    if(classes) {
      let selectedClass = classes.find( (cls) => cls.id == this.state.class_id );
      if(selectedClass) {
        streams = selectedClass.streams;
      }
    }

    return <div className="container-fluid">
      <div className="row">
        <div className="col-md-12">
        </div>
      </div>
      { this.state.dataTableConfig.columns.length > 0 && <DataTable ref={this.dataTable} title="Performance" config={this.state.dataTableConfig} toolbar={
        <>
          <select onChange={this.filterClass} value={this.state.class_id} id='filterByClass' className="form-control col-4">
            <option value=''>-- Select Classes--</option>
            {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
          </select>
          <select onChange={this.filterStream} value={this.state.stream_name} id='filterByStream' className="form-control col-4">
            <option value=''>-- Select Stream--</option>
            {streams && streams.map( (stream) => <option key={stream.id} value={stream.name}>{stream.name}</option> )}
          </select>
          <div className="form-group col-4">
            <input type="text" className="form-control" onChange={this.filterString} placeholder="Search" />
          </div>
        </>
      }>
        {this.state.dataTableConfig.columns.map( (value,index) => <th key={index}>{value.name}</th> )}
      </DataTable> }
    </div>

  }
}
