import React, { Component } from 'react';
import { Route, Redirect } from "react-router-dom";

import AnimatedSwitch from '../components/animated-switch';

import Exams, {NewExam, EditExam} from './Examination';
import ExamGroup, {NewExamGroup, EditExamGroup} from './ExamGroup';
import Record from './Record';
import Performance from './Performance';
import Analysis from './Analysis';


export default class ExaminationPages extends Component {
	render() {
		let match = this.props.match;
		return <AnimatedSwitch>
				<Route exact path={`${match.path}/performance`} render={(props)=> <Performance {...props} /> } />
				<Route exact path={`${match.path}/analysis`} render={(props)=> <Analysis {...props} /> } />

				<Route exact path={`${match.path}/exam-groups/:examGroupId/exams/new`} render={(props)=> <NewExam {...props} /> } />
				<Route exact path={`${match.path}/exam-groups/:examGroupId/exams/:examId`} render={(props)=> <EditExam {...props} /> } />
				<Route exact path={`${match.path}/exam-groups/:examGroupId/exams`} render={(props)=> <Exams {...props} /> } />
				<Route exact path={`${match.path}/exam-groups/:examGroupId/exams/:examId/record`} render={(props)=> <Record {...props} /> } />

				<Route exact path={`${match.path}/exam-groups/new`} render={(props)=> <NewExamGroup {...props} /> } />
				<Route exact path={`${match.path}/exam-groups/:examGroupId`} render={(props)=> <EditExamGroup {...props} /> } />
				<Route exact path={`${match.path}/exam-groups`} render={(props)=> <ExamGroup {...props} /> } />

				<Route exact path={`${match.path}`}><Redirect to={`${match.path}/exam-groups`} /></Route>

			</AnimatedSwitch>
	}
};
