import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';

import ExamGroupForm from '../components/ExamGroupForm';
import ShowNotification from '../components/ShowNotification';
import DataTable from '../components/DataTable';

import getCookie from '../utils/getCookie';

@inject('examGroupStore')
export default class ExamGroup extends Component {
  constructor(props){
    super(props);
    this.state = { classes: null, class_id: '', subject_id: '', date: '' };
    this.dataTable = React.createRef();

    var self = this;
    window.examListActionEdit = this.edit;
    window.examListActionShowExams = this.showExams;
    this.dataTableConfig = {
      serverSide: false,
      processing: false,
      ajax : function (params, callback, settings) {
          self.props.examGroupStore.list(params,true).then(examGroups=>{
              callback({data: examGroups});
              return examGroups;
          })
      },
      columns: [
        { data: 'name', name: 'name', 'class': 'text-center'},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
            <a onclick="examListActionShowExams('${data.id}')" class="btn btn-link btn-warning details"><i class="nc-icon nc-bullet-list-67 icon-bold"></i></a>
          </span>`}},
        { data: null, 'class': 'text-center', render: function(data,type,row) {
          return `<span>
            <a onclick="examListActionEdit('${data.id}')" class="btn btn-link btn-warning details"><i class="fa fa-edit"></i></a>
          </span>`}
        }]
    };
  }

  inputChanged(field,event) {
    let val = event.target.value;
    this.setState({[field]: val}, this.filterChanged);
  }

  filterChanged = () => {
    this.dataTable.current.draw();
  }

  edit = (examGroup) => {
    this.props.history.push(`${this.props.match.url}/${examGroup}`);
  }

  showExams = (examGroup) => {
    this.props.history.push(`${this.props.match.url}/${examGroup}/exams`);
  }

  render() {
    let match = this.props.match;

    return <div className="container-fluid">
      <div className="row">
        <div className="col-md-12">
          <span className="pull-right">
            <Link to={`${match.url}/new`} className="btn btn-success details" >New Exam Group</Link>
          </span>
        </div>
      </div>
      <DataTable ref={this.dataTable} config={this.dataTableConfig} title="Exams Group">
        <th>Name</th>
        <th>Exams</th>
        <th>Action</th>
      </DataTable>
    </div>

  }
}

@inject('examGroupStore')
export class NewExamGroup extends Component {
  state = { error: {} };
  submit = (data) => {
    return this.props.examGroupStore.create(data).then( examGroup => {
      this.setState({error: {}, data: examGroup})
      return examGroup;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  close = () => {
    this.props.history.push('/school/examination');
  }
  render() {
    return <ExamGroupForm error={this.state.error} submit={this.submit} close={this.close} />
  }
}

@inject('examGroupStore')
export class EditExamGroup extends Component {
  state = { error: {}, data: {} };

  componentDidMount() {
  if(this.props.location.state && this.props.location.state.student){
    this.setState({data: this.props.location.state.student})
  }else if(this.props.match.params.examGroupId){
    this.props.examGroupStore.get(this.props.match.params.examGroupId, true).then(examGroup => {
      this.setState({data: examGroup});
    },error=>this.setState({error}))
  }
  
  }

  submit = (data) => {
    return this.props.examGroupStore.update(data).then( examGroup => {
      this.setState({error: {}, data: examGroup})
      return examGroup;
    }, error => {
      this.setState({error});
      return Promise.reject(error);
    })
  }

  delete = () => {
    return this.props.examGroupStore.delete(this.props.match.params.examGroupId);
  }

  close = () => {
    this.props.history.push('/school/examination');
  }

  render() {
    return <ExamGroupForm edit={true} error={this.state.error} data={this.state.data} submit={this.submit} delete={this.delete} close={this.close} />
  }
}