import React, { Component } from 'react';
import lodash from 'lodash';

import ShowNotification from './ShowNotification';
import confirmAction from '../utils/confirmAction';

export default class ExamForm extends Component {
	defaults = {
		name: '',
	};

	state = { error: {}, loading: false, data: {...this.defaults}
	};


	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Exam Group Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Exam Group Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	delete = () => {
    confirmAction( () => {
      return this.props.delete().then(res=>{
        this.props.close();
        return {success: 'Deleted!', succes_text: 'Exam group was deleted'};
      }, error => {
        throw {error: "Exam group couldn't be deleted."};
      })
    }, {loading_title: 'Deleting...', loading_text: 'Deleting Exam group', canceled: "Deleting Exam group Operation Cancelled", consequence: 'Exam group with its accossiated exams will be deleted'});
  }

	render() {
		let error = this.state.error || {};
		let nameInvalid = error.errors && error.errors.name ? 'is-invalid' : '';

		return <div className="container-fluid" >
			{this.props.edit && <div className="row">
          <div className="col-md-12">
            <span className="pull-right">
              <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
            </span>
          </div>
      </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Exam Group Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="form-group">
					        <label>Exam Group Name</label>
					        <input value={this.state.data.name} onChange={this.inputChanged.bind(this,'name')} type="text" placeholder="Exam Group Name" className={`form-control ${nameInvalid}`} />
					        {nameInvalid && error.errors.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
									<p className="help-block help-block-error"></p>
					      </div>

					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>
					</div>
		    </div>
		  </div>
		</div>

	}
}