import React, { Component } from 'react';
import { inject } from 'mobx-react';
import lodash from 'lodash';
import $ from 'jquery';

import ShowNotification from './ShowNotification';
import confirmAction from '../utils/confirmAction';

@inject('classesStore')
@inject('subjectStore')
export default class ExamForm extends Component {
	defaults = {
		name: '',
		subject_id: '',
		total: '',
		percentage: '',
		class_id: ''
	};

	state = { error: {}, loading: false, classes: null, loading: false, data: {...this.defaults}
	};

	componentDidMount() {
    this.props.classesStore.list(true).then(classes=>{
    	this.setState({classes});
    });

    this.props.subjectStore.list(true).then(subjects=>{
    	this.setState({subjects});
    });
	}

	componentDidUpdate(prevProps,prevState) {
		if(this.props.edit){
			if(!lodash.isEqual(this.props.data, prevProps.data)){
				this.setState({data: {...this.state.data,...this.props.data}})
			}
		}
		if(!lodash.isEqual(this.props.error,prevProps.error)){
			this.setState({error: {...this.state.error,...this.props.error}})
		}
	}

	inputChanged = (field,event) => {
		let val = event.target.value;
		this.setState( (state) => state.data[field] = val);
	}

	saveContinue = () => {
		this.submit().then(()=>{
			this.setState({data: {...this.defaults},error: {}});
		})
	}

	save = () => {
		this.submit().then(()=>this.props.close());
	}

	apply = () => {
		this.submit().then(()=>{
			this.setState({error: {}});
		})
	}

	submit = () => {
		let data = this.state.data;
		this.setState({loading: true});
		return this.props.submit(data).then( res => {
			if(this.props.edit) {
				ShowNotification("Exam Successfully Edited", "success", "nc-check-2");
			}else {
				ShowNotification("Exam Created Successfully", "success", "nc-check-2");
			}
		}, error => {
			this.setState({error});
			return Promise.reject(error);
		}).finally(_=>this.setState({loading:false}));
	}

	delete = () => {
    confirmAction( () => {
      return this.props.delete().then(res=>{
        this.props.close();
        return {success: 'Deleted!', succes_text: 'Exam was deleted'};
      }, error => {
        throw {error: "Exam couldn't be deleted."};
      })
    }, {loading_title: 'Deleting...', loading_text: 'Deleting Exam', canceled: "Deleting Exam Operation Cancelled", consequence: 'Exam will be deleted'});
  }

	render() {
		let { classes, subjects } = this.state;

		let error = this.state.error || {};
		let nameInvalid='', subjectInvalid='', totalInvalid='', percentageInvalid='', classInvalid='';
		if(error.errors) {
			nameInvalid = error.errors.name ? 'is-invalid' : '';
			subjectInvalid = error.errors.subject_id ? 'is-invalid' : '';
			totalInvalid = error.errors.total ? 'is-invalid' : '';
			percentageInvalid = error.errors.percentage ? 'is-invalid' : '';
			classInvalid = error.errors.class_id ? 'is-invalid' : '';
		}

		return <div className="container-fluid" ref={this.elRef} >
			{this.props.edit && <div className="row">
          <div className="col-md-12">
            <span className="pull-right">
              <button type="button" className="btn btn-danger" onClick={this.delete} >Delete</button>
            </span>
          </div>
      </div>}
		  <div className="row">
		    <div className="col-md-12">
		      <div className="card stacked-form">
					  <div className="card-header ">
					    <h4 className="card-title">Exam Details</h4>
					  </div>
					  <div className="card-body ">
					    <form method="#" action="#">
					    	{error.message && <div className="alert alert-danger">
                    <span>
                        <b> Error - </b> {error.message}
                    </span>
                </div>}
					    	<div className="row">
						      <div className="form-group col-md-6">
						        <label>Exam Name</label>
						        <input value={this.state.data.name} onChange={this.inputChanged.bind(this,'name')} type="text" placeholder="Exam Name" className={`form-control ${nameInvalid}`} />
						        {nameInvalid && error.errors.name.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="form-group col-md-6">
						        <label>Subject</label>
						        <select disabled={!subjects} value={this.state.data.subject_id} onChange={this.inputChanged.bind(this,'subject_id')} className={`form-control ${subjectInvalid}`} >
						        	<option value="">Select Subject</option>
                      {subjects && subjects.map( (subject) => <option key={subject.id} value={subject.id}>{subject.name}</option> )}
                    </select>
						        {subjectInvalid && error.errors.subject_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>

					      <div className="row">
					      	<div className="form-group col-md-6">
						        <label>Total</label>
						        <input value={this.state.data.total} onChange={this.inputChanged.bind(this,'total')} type="text" placeholder="Total" className={`form-control ${totalInvalid}`} />
						        {totalInvalid && error.errors.total.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>

						      <div className="form-group col-md-6">
						        <label>Percentage</label>
						        <input value={this.state.data.percentage} onChange={this.inputChanged.bind(this,'percentage')} type="text" placeholder="Percentage" className={`form-control ${percentageInvalid}`} />
						        {percentageInvalid && error.errors.percentage.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
						    </div>

					      <div className="row">
						      <div className="form-group col-md-6">
						        <label>Class</label>
						        <select disabled={!classes} value={this.state.data.class_id} onChange={this.inputChanged.bind(this,'class_id')} name="class" className={`form-control ${classInvalid}`} >
						        	<option value="">Select Class</option>
                      {classes && classes.map( (cls) => <option key={cls.id} value={cls.id}>{cls.name}</option> )}
                    </select>
						        {classInvalid && error.errors.class_id.map((value,index) => <span key={index} className="invalid-feedback" role="alert"><strong>{value}</strong></span>)}
										<p className="help-block help-block-error"></p>
						      </div>
					      </div>


					    </form>
					  </div>
					  <div className="card-footer ">
					  	<div className="row">
						  	{this.props.edit ? <div className="col-sm-2">
			            	<input onClick={this.apply} type="button" value="APPLY" disabled={this.state.loading} className="btn btn-primary" />
			          	</div> :
			          	<div className="col-sm-3">
			            	<input onClick={this.saveContinue} type="button" value="SAVE AND CONTINUE" disabled={this.state.loading} className="btn btn-primary" />
			          	</div>
			          }
			          <div className="col-sm-2">
			            <input onClick={this.save} type="button" value="SAVE" disabled={this.state.loading} className="btn btn-success" />
			          </div>
			          <div className="col-sm-2">
			            <input onClick={this.props.close} type="button" value="CLOSE" disabled={this.state.loading} className="btn btn-warning" />
			          </div>
					    </div>
					  </div>


					  


					</div>
		    </div>
		  </div>
		</div>

	}
}